.screen {
  min-height: 800px;
  height: 100vh;
  @include media-breakpoint-down(xxl) {
    min-height: 600px;
  }
  @include media-breakpoint-down(lg) {
    min-height: 800px;
  }
  @include media-breakpoint-down(sm) {
    min-height: 550px;
  }
}

.homepage {
  position: relative;
  &-scene {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    &__container {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      width: 100%;
      height: 100%;
    }
    &__layers, &__triggers {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    &__triggers {
      pointer-events: none;
      opacity: 0;
      visibility: hidden;
    }
    &__layer, &__bg {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: bottom center;
    }
    &__layer {
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }
    &__trigger {
      position: absolute;
      background-color: rgba(255, 0, 0, 0.3);
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-weight: 700;
      font-size: 20px;
      border: 1px solid #fff;
      &:first-child {
        height: 50%;
        bottom: 0;
        width: 10%;
      }
      &:nth-child(2) {
        height: 50%;
        bottom: 0;
        width: 7%;
        left: 10%;
      }
      &:nth-child(3) {
        height: 30%;
        bottom: 0;
        width: 7%;
        left: 17%;
      }
      &:nth-child(4) {
        height: 30%;
        bottom: 0;
        width: 7%;
        left: 24%;
      }
      &:nth-child(5) {
        height: 30%;
        bottom: 0;
        width: 6%;
        left: 31%;
      }
      &:nth-child(6) {
        height: 50%;
        bottom: 0;
        width: 10%;
        left: 37%;
      }
      &:nth-child(7) {
        height: 100%;
        width: 53%;
        left: 47%;
      }
      &:nth-child(8) {
        height: 44%;
        top: 8%;
        width: 13%;
        left: 45%;
      }
      &:nth-child(9) {
        height: 34%;
        width: 24%;
        bottom: 0;
        right: 0;
      }
      &:nth-child(10) {
        height: 25%;
        width: 14%;
        top: 23%;
        right: 9%;
      }
    }
  }
  &__container {
    position: relative;
    z-index: 2;
    height: 100%;
    flex-direction: column;
    display: flex;
  }
  &__top {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 90px;
  }
  &__bottom {
    flex: 0 0 auto;
    padding-bottom: 65px;
  }
  &__label {
    margin: 0 0 70px 0;
  }
  &__title {
    margin: 0;
  }
  &__request {
    .button {
      margin-top: 38px;
    }
  }
  @include media-breakpoint-up(xl) {
    &__request {
      &>.container {
        padding: 0;
        max-width: 100%;
      }
    }
  }
  @include media-breakpoint-down(xxl) {
    &__label {
      margin-bottom: 50px;
    }
    &__top {
      padding-top: 70px;
    }
    &__bottom {
      padding-bottom: 50px;
    }
  }
  @include media-breakpoint-down(xl) {
    &__request {
      position: absolute;
      top: 100%;
    }
    &__bottom {
      padding-bottom: 40px;
    }
    &__label {
      margin-bottom: 25px;
    }
  }
  @include media-breakpoint-down(lg) {
    &__bottom {
      padding: 0;
      max-width: 100%;
      & > .row {
        margin: 0;
        & > * {
          padding: 0;
        }
      }
    }
  }
  @include media-breakpoint-down(sm) {
    &__top {
      padding-top: 50px;
      padding-bottom: 30px;
    }
  }
}

.items-slider {
  border-radius: 20px;
  box-shadow: inset -2px -2px 1px rgba(255, 255, 255, 0.05);
  background-color: var(--color-bg-transparent);
  backdrop-filter: $blur1;
  &__container {
    display: flex;
    position: relative;
  }
  &__images {
    flex: 0 0 150px;
    margin-right: 50px;
    position: relative;
    margin-bottom: 60px;
  }
  &__arrow {
    position: absolute;
    width: 50px;
    height: 50px;
    display: flex;
    top: 50%;
    transform: translateY(-50%);
    color: var(--color-grayish);
    transition: color $trs1;
    z-index: 2;
    .icon {
      width: 22px;
      height: 22px;
      margin: auto;
    }
    &[data-touch], &[data-hover] {
      color: var(--color-text);
    }
  }
  &__next {
    right: 10px;
  }
  &__prev {
    left: 10px;
    .icon {
      transform: rotate(180deg);
    }
  }
  &__image {
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    img {
      display: block;
      width: 100%;
      height: auto;
      bottom: 0;
      left: 0;
      position: absolute;
      transform: translateY(20px);
    }
  }
  &__button {
    max-width: 100%;
  }
  &__element {
    flex: 1 1 100%;
    margin-bottom: 60px;
    position: relative;
    z-index: 2;
  }
  &__buttons {
    position: absolute;
    padding-left: 200px;
    bottom: 30px;
  }
  .splide__pagination {
    position: absolute;
    width: 150px;
    transform: translateX(-100%);
    left: -50px;
    bottom: -47px;
  }

  @include media-breakpoint-up(lg) {
    &__container {
      padding: 30px 100px;
      max-width: 100%;
    }
  }
  @include media-breakpoint-up(xxl) {
    &__container {
      padding: 30px 110px;
    }
  }
  @include media-breakpoint-down(xxl) {
    &__images {
      flex: 0 0 140px;
    }
    &__buttons {
      padding-left: 190px;
    }
    .splide__pagination {
      width: 140px;
    }
  }
  @include media-breakpoint-down(lg) {
    border-radius: 0;
    &__container {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    &__images {
      margin-left: 60px;
      margin-bottom: 75px;
    }
    &__element {
      margin-right: 60px;
      margin-bottom: 75px;
    }
    &__buttons {
      padding-left: 250px;
    }
    &__next {
      right: 6px;
    }
    &__prev {
      left: 6px;
    }
    .splide__pagination {
      bottom: -69px;
    }
  }
  @include media-breakpoint-down(md) {
    &__images {
      margin-left: 0;
    }
    &__element {
      margin-right: 0;
    }
    &__buttons {
      padding-left: 190px;
    }
    &__arrow {
      display: none;
    }
  }
  @include media-breakpoint-down(sm) {
    &__container {
      padding-top: 16px;
      padding-bottom: 24px;
    }
    &__images {
      margin-right: 20px;
      flex: 0 0 94px;
    }
    &__buttons {
      padding-left: 0;
      display: flex;
      justify-content: center;
      width: 100%;
      left: 0;
      bottom: 24px;
    }
    &__image {
      img {
        transform: translateY(-20px);
      }
    }
    .splide__pagination {
      bottom: 3px;
      width: 94px;
      left: -20px;
    }
  }
}